@font-face {
  font-family: Roboto;
  src: url(../fonts/Roboto-Black.ttf) format("ttf");
  font-weight: 900;
  font-style: normal; }

@font-face {
  font-family: Roboto;
  src: url(../fonts/Roboto-Bold.ttf) format("ttf");
  font-weight: 800;
  font-style: normal; }

@font-face {
  font-family: Roboto;
  src: url(../fonts/Roboto-Medium.ttf) format("ttf");
  font-weight: 600;
  font-style: normal; }

@font-face {
  font-family: Roboto;
  src: url(../fonts/Roboto-Regular.ttf) format("ttf");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: Roboto;
  src: url(../fonts/Roboto-Italic.ttf) format("ttf");
  font-weight: 400;
  font-style: italic; }

@font-face {
  font-family: Roboto;
  src: url(../fonts/Roboto-Light.ttf) format("ttf");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: Roboto;
  src: url(../fonts/Roboto-Thin.ttf) format("ttf");
  font-weight: 100;
  font-style: normal; }

* {
  box-sizing: border-box; }

body {
  margin: 0;
  min-width: 320px;
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  height: 100%;
  color: #212121; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.page {
  max-width: 1920px;
  margin: 0 auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  letter-spacing: 1px;
  padding-top: 90px; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 90px; }
  .header__logo {
    text-align: center; }
    .header__logo img {
      width: 200px; }
  .header__cart {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%); }
  .header .nav {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none; }
    .header .nav .nav__btn-toogle {
      border: none;
      background-color: transparent;
      outline: 0;
      position: absolute;
      left: 20px;
      top: 30px;
      width: 40px;
      height: 20px;
      z-index: 999; }
      .header .nav .nav__btn-toogle span {
        position: absolute;
        left: 0;
        right: 0;
        width: 100%;
        height: 1px;
        background-color: #212121;
        transition: all .2s linear; }
        .header .nav .nav__btn-toogle span:first-child {
          top: 0; }
        .header .nav .nav__btn-toogle span:nth-child(2) {
          top: 50%; }
        .header .nav .nav__btn-toogle span:last-child {
          bottom: 0; }
      .header .nav .nav__btn-toogle--open span:first-child {
        top: 50%;
        transform: rotate(45deg); }
      .header .nav .nav__btn-toogle--open span:nth-child(2) {
        opacity: 0; }
      .header .nav .nav__btn-toogle--open span:last-child {
        top: 50%;
        transform: rotate(-45deg); }
      @media screen and (min-width: 990px) {
        .header .nav .nav__btn-toogle {
          display: none; } }
    .header .nav__menu-wrap {
      position: fixed;
      top: 90px;
      bottom: 0;
      left: -100%;
      z-index: 99;
      background-color: #fff;
      padding: 20px;
      width: 100%;
      transition: all .2s ease-in-out;
      overflow-y: auto; }
      .header .nav__menu-wrap--open {
        left: 0; }
    .header .nav__menu {
      list-style: none;
      margin: 0;
      padding: 0; }
      .header .nav__menu .menu-item a {
        display: block;
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 600;
        padding: 10px 5px;
        border-bottom: 1px solid #ddd; }
